body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(226, 245, 255);
  background-size: cover;
  background-repeat: no-repeat;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
li a {
  color: rgb(7, 7, 32);
  background-color: rgb(175, 209, 230);
  padding: 3% 39%;
}
.navbar {
  padding: 0.1rem 1rem;
}

a:hover {
  background-color: rgb(219, 128, 101);
  text-decoration: none;
  color: white;
}

h2 {
  color: rgb(23, 144, 201);
  padding-top: 8%;
  font-size: 2.8em;
  font-weight: 500;
  margin-bottom: 10%;
}
li {
  margin-right: 1%;
}
.one,
.two {
  width: 31.3%;
}
.two {
  margin-left: 4.6%;
}

.nav-tabs {
  border-bottom: 0;
}

div.thumbnail {
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  padding-top: 100%;
}

.btn {
  padding-top: 0.135rem !important;
  padding-bottom: 0.135rem !important;
}

.view {
    margin-right: 3px
}
.search {
  float: right;
  padding: 1% 3%;
  margin: 10px 0;
}

